table {
    border-collapse: collapse;
}

tr:hover {
    background-color: lightblue;
}

td, th {
    border: 0.5px solid black;
    text-align: center;
    padding: 0.3rem 0.8rem;
    user-select: none;
}

tr, td, th {
    transition: background-color 0.1s;
}

td:hover, th:hover {
    background-color: rgb(190, 200, 255);
}